import React, { useState } from 'react'
import guitar from '../assets/png/const_acoustic.png'
import guitar_notes from '../assets/png/const_acoustic_det.png'
import '../css-files/acoustic.css'

export default function Acoustic(props) {
	let [selected, setSelectet] = useState(0);

	function selectPart(nr) {
		if (nr === selected) {
			setSelectet(0);
			return;
		}
		setSelectet(nr);

	}

  return (
    <div className={!props.transition ? 'acoustic' : 'acoustic off'}>
		<div className="title">acustic.</div>
		<div className="guitar">
			<img className="pic" src={guitar} alt="" />
			<span className={selected === 1 ? "select body selected" : "select body"} onClick={() => selectPart(1)}></span>
			<span className={selected === 2 ? "select neck selected" : "select neck"} onClick={() => selectPart(2)}></span>
			<span className={selected === 3 ? "select headstock selected" : "select headstock"} onClick={() => selectPart(3)} ></span>
		</div>
		<div className="body-desc">
			{selected === 0 && <p>selecteaz o <b>component</b> pentru a afia <b>informaii.</b></p>}
			{selected === 1 && <p><b>Corpul chitarei</b> este o cutie rezonant format din <b>blatur</b> (partea superioar), <b>spate</b> i <b>laterale</b>, realizat n general din lemn masiv. Blatura conine punile i gurile de sunet, iar spatele reflect i amplific sunetul. <b>Forma</b> i <b>dimensiunile</b> corpului pot influena sunetul i exist mai multe variante disponibile, precum dreadnought, jumbo, concert, auditorium sau parlor.</p>}
			{selected === 2 && <p><b>Gtul chitarei</b> este partea lung i subire care susine tastatura i se extinde de la corpul chitarei. Este de obicei construit din lemn dur i poate avea o form plat sau rotunjit. <b>Tastatura</b> este o plac de lemn aezat pe partea superioar a gtului, care conine fante pentru fiecare dintre corzi i puncte de referin pentru chitariti. Lungimea i forma gtului chitarei variaz n funcie de modelul instrumentului.</p>}
			{selected === 3 && <p><b>Headstock-ul</b> chitarei este partea de la captul gtului care conine mecanismele de acordaj ale corzilor i este decorat cu ornamente. Este important nu doar pentru aspectul estetic al chitarei, ci i pentru funcionalitatea sa, permind chitaristului s <b>ajusteze tensiunea corzilor</b>.</p>}
		</div>
	</div>
  )
}